body {
  font-family: "Open Sans", sans-serif;
  font-weight: normal; }

.my-cart {
  background: #f6f6f7;
  margin: 15px 0px 40px; }
  .my-cart .my-cart-content {
    width: 100%;
    margin: 0 auto;
    margin-right: 10%;
    position: relative;
    text-align: right; }
    .my-cart .my-cart-content .btn-my-cart {
      position: relative;
      display: inline-block;
      z-index: 2;
      text-align: center; }
      .my-cart .my-cart-content .btn-my-cart:hover .my-cart-text {
        background: white;
        border: 1px solid #cccccc;
        line-height: 24px;
        border-bottom: none;
        color: #222222; }
      .my-cart .my-cart-content .btn-my-cart:hover ul.my-items {
        display: block; }
      .my-cart .my-cart-content .btn-my-cart .my-cart-text {
        color: #888888;
        font-size: 12px;
        line-height: 25px;
        width: 100%;
        display: block;
        position: relative;
        z-index: 5;
        padding: 0px 10px; }
      .my-cart .my-cart-content .btn-my-cart .my-cart-icon {
        display: none; }
      .my-cart .my-cart-content .btn-my-cart ul.my-items {
        display: none;
        width: 250px;
        border: 1px solid #cccccc;
        background: white;
        position: absolute;
        top: 24px;
        right: 0;
        z-index: 1;
        padding: 20px;
        padding-bottom: 0px;
        margin: 0px; }
        .my-cart .my-cart-content .btn-my-cart ul.my-items li {
          list-style-type: none;
          width: 100%;
          margin-bottom: 20px; }
          .my-cart .my-cart-content .btn-my-cart ul.my-items li div {
            display: inline-block; }
          .my-cart .my-cart-content .btn-my-cart ul.my-items li .my-item-img {
            width: 40%;
            text-align: center; }
            .my-cart .my-cart-content .btn-my-cart ul.my-items li .my-item-img img {
              width: 80px; }
          .my-cart .my-cart-content .btn-my-cart ul.my-items li .my-item-details {
            width: 60%;
            vertical-align: top; }
            .my-cart .my-cart-content .btn-my-cart ul.my-items li .my-item-details p {
              color: #222222;
              text-align: left;
              margin-top: 5px;
              line-height: 0px;
              margin-bottom: 30px;
              font-size: 15px; }

@media screen and (max-device-width: 762px) {
  .my-cart-title {
    display: none; }
  .my-cart .my-cart-content .btn-my-cart .my-cart-icon {
    display: inline-block; } }

body {
  font-family: "Open Sans", sans-serif;
  font-weight: normal;
  padding-bottom: 20px; }

.section-img {
  text-align: center;
  margin-bottom: 15px; }
  .section-img img {
    width: 100%;
    max-width: 400px; }

.prod-name {
  font-weight: bold;
  font-size: 20px; }

.prod-amount {
  font-size: 14px;
  font-weight: bold;
  line-height: 35px;
  border-top: 1px solid #f8f8f8;
  border-bottom: 1px solid #f8f8f8; }

.prod-info {
  font-size: 13px;
  line-height: 20px;
  color: #888888;
  margin-bottom: 30px; }

.prod-sizes {
  color: #888888;
  font-size: 12px;
  text-transform: uppercase;
  margin-bottom: 20px; }
  .prod-sizes .req-star {
    font-weight: bold; }
    .prod-sizes .req-star:after {
      content: "*";
      color: #c90000; }
  .prod-sizes .selected-size {
    color: #222222; }
  .prod-sizes ul {
    -webkit-padding-start: 0px;
            padding-inline-start: 0px; }
    .prod-sizes ul li {
      display: inline-block;
      border: 1px solid #cccccc;
      width: 40px;
      height: 40px;
      text-align: center;
      line-height: 40px;
      cursor: pointer;
      margin: 0 2px; }
      .prod-sizes ul li:hover, .prod-sizes ul li:active {
        border: 2px solid #222222;
        width: 38px;
        height: 38px;
        line-height: 38px;
        color: #222222; }

.btn-add-cart {
  text-transform: uppercase; }

